.content__avatar-overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 120px;
  height: 120px;
  border-radius: 50%;
}

.content__avatar-overlay:hover {
  background: url(../../../images/logo/Vector.svg) no-repeat center,
    rgba(0, 0, 0, 0.5);
  cursor: pointer;
  z-index: 0;
}
